
.xs-flex-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.xs-flex-box .xs-flex-item {
    -webkit-box-flex: 0;
        -ms-flex: 0 33.33%;
            flex: 0 33.33%;
    overflow: hidden;
}
.bookrack .header {
  text-align: left;
  padding: 10px;
}
.bookrack .header .search {
    float: right;
}
.bookrack .bookrack-item {
  padding: 0 10px;
  text-align: left;
  margin-top: 15px;
}
.bookrack .bookrack-item .img-box {
    height: 150px;
}
.bookrack .bookrack-item .cover {
    width: 100%;
    height: auto;
    max-height: 150px;
}
.bookrack .bookrack-item .title {
    font-size: 14px;
    height: 20px;
    line-height: 20px;
    margin-top: 5px;
}
.bookrack .bookrack-item .tocname {
    font-size: 12px;
    height: 16px;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    margin-top: 5px;
    white-space: nowrap;
}
.bookrack .bookrack-item .more {
    position: relative;
    display: block;
    background-color: #ddd;
    text-align: center;
    margin-bottom: 60px;
    height: 150px;
    line-height: 150px;
    font-size: 60px;
    color: #fff;
}



