
body {
  margin: 0;
}
ul {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
.active {
  color: #3dd7e3;
}
.before-border {
  position: relative;
}
.before-border:before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #D9D9D9;
    color: #D9D9D9;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.after-border {
  position: relative;
}
.after-border:after {
    content: " ";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    border-bottom: 1px solid #D9D9D9;
    color: #D9D9D9;
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.text-over {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.popup-mask {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  background: rgba(0, 0, 0, 0.5);
  -webkit-transition: opacity .3s linear;
  transition: opacity .3s linear;
}
.popup-mask.show {
    opacity: 1;
    z-index: 500;
}
.popup-view {
  -webkit-transition: all .15s;
  transition: all .15s;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: auto;
  z-index: 501;
  background-color: #fff;
}
.popup-enter-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.popup-leave-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
