
.xs-loading-toast {
  position: relative;
}
.xs-loading-toast .xs-mask {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: transparent;
    z-index: 999;
}
.xs-loading-toast .xs-toast {
    position: fixed;
    background-color: rgba(17, 17, 17, 0.7);
    width: 120px;
    height: 120px;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    text-align: center;
    color: #fff;
    z-index: 1000;
    border-radius: 5px;
}
.xs-loading-toast .xs-toast-icon {
    margin: 25px 0;
    display: block;
}
.xs-loading-toast .xs-toast-icon:before {
      display: block;
      font-size: 20px;
      -webkit-animation: xsLoadinng 1s infinite linear;
              animation: xsLoadinng 1s infinite linear;
}
.xs-loading-toast .xs-icon-loading1:before, .xs-loading-toast .xs-icon-loading2:before {
    -webkit-animation: xsLoadinng1 2s infinite linear;
            animation: xsLoadinng1 2s infinite linear;
}
.xs-loading-toast .xs-toast-content {
    font-size: 18px;
}
@-webkit-keyframes xsLoadinng {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes xsLoadinng {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes xsLoadinng1 {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
75% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes xsLoadinng1 {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
75% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



