
.read-view {
  padding: 0 15px;
  height: 100vh;
  position: relative;
  z-index: 1;
  color: #fff;
}
.read-view .head {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    text-align: center;
}
.read-view .read-content {
    position: absolute;
    top: 30px;
    bottom: 20px;
    left: 15px;
    right: 15px;
    overflow: hidden;
}
.read-view .read-content p {
      margin: 2px 0;
      text-indent: 2em;
}
.read-view .read-content article {
      height: 100%;
      overflow: hidden;
}
.read-view .read-content .read {
      text-align: justify;
      -webkit-column-count: 1;
         -moz-column-count: 1;
              column-count: 1;
      -webkit-column-gap: 0;
         -moz-column-gap: 0;
              column-gap: 0;
      -webkit-column-width: cacl(100vw);
         -moz-column-width: cacl(100vw);
              column-width: cacl(100vw);
      -webkit-columns: 100vw 1;
         -moz-columns: 100vw 1;
              columns: 100vw 1;
      height: 100%;
}
.read-view .count {
    position: fixed;
    bottom: 0;
    left: 15px;
    right: 15px;
    text-align: left;
}
.read-view .action-view .left {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 50%;
    height: 100%;
}
.read-view .action-view .right {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50%;
    height: 100%;
}
.read-view .action-view .center {
    position: absolute;
    width: 200px;
    height: 200px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    z-index: 1;
}
.option-view {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}
.option-view .head {
    background-color: transparent;
    position: relative;
    color: #fff;
    height: 30px;
    padding: 3px 0;
    line-height: 30px;
    text-align: left;
}
.option-view .head a {
      color: #fff;
      font-size: 20px;
      vertical-align: -2px;
      margin: 0 15px;
}
.option-view .head .exchange {
      float: right;
      margin-right: 15px;
}
.option-view .source {
    position: relative;
    left: 20%;
    right: 0;
    max-height: 300px;
    background-color: white;
    color: #000;
    text-align: left;
    margin-left: -15px;
    padding: 15px;
    overflow-y: scroll;
}
.option-view .popup-view {
    bottom: 50px;
    background-color: white;
    color: #000;
    overflow: scroll;
    text-align: left;
    z-index: 2;
}
.option-view .popup-view .tocs-header {
      padding: 0 15px;
      background-color: white;
      color: #000;
}
.option-view .opt-view {
    position: fixed;
    bottom: 50px;
    left: 0;
    right: 0;
    background-color: white;
    padding: 10px;
    color: #000;
}
.option-view .bg-view {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 10px -5px;
}
.option-view .bg-view .bg-item {
      height: 30px;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 0 5px;
}
.option-view .bg-view .bg-item input {
        display: none;
}
.option-view .bg-view .bg-item label {
        width: 100%;
        height: 100%;
        border-radius: 3px;
        border: 1px solid #ddd;
        display: inline-block;
}
.option-view .footer {
    background-color: transparent;
    position: absolute;
    bottom: 0;
    height: 30px;
    padding: 10px 0;
    width: 100%;
    z-index: 3;
}
.option-view .footer .btn-group {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.option-view .footer .btn-group a {
        -webkit-box-flex: 20%;
            -ms-flex: 20%;
                flex: 20%;
        color: #fff;
}
.option-view .footer .btn-group h4 {
        font-size: 14px;
        margin: 0;
        line-height: 1;
}



